.container {
    font-family: Arial, sans-serif;
}

header {
    background-color: #007acc;
    color: white;
    text-align: center;
    padding: 20px 0;
}

main {
    padding: 20px;
}

.feature {
    margin-bottom: 40px;
}

.feature h2 {
    color: #007acc;
}

.feature p {
    font-size: 18px;
    line-height: 1.6;
    margin-top: 10px;
}

.cta {
    text-align: center;
    margin-top: 40px;
}

.cta h2 {
    color: #007acc;
}

.cta p {
    font-size: 18px;
    line-height: 1.6;
    margin-top: 10px;
}

.download-button {
    display: inline-block;
    padding: 10px 20px;
    background-color: #007acc;
    color: white;
    text-decoration: none;
    font-size: 16px;
    border-radius: 5px;
    transition: background-color 0.3s ease;
}

.download-button:hover {
    background-color: #005fa6;
}

footer {
    text-align: center;
    padding: 20px 0;
    background-color: #f5f5f5;
    color: #555;
}
